import { graphql } from "gatsby";
import { StaticImage } from "gatsby-plugin-image";
import React, { useEffect, useState } from "react";
import Header from "../components/Header";
import Music from "../components/Music";
import Scene from "../components/Scene";

const IndexPage = ({ data }: { data: any }) => {
  const [loading, setLoading] = useState(true);
  const [showPreview, setShowPreview] = useState(false);
  const [scenes, setScenes] = useState([]);
  const [scene, setScene] = useState(null);

  useEffect(() => {
    setScenes(data.strapiQuest.Scenes);
    setScene(data.strapiQuest.Scenes[0]);
    setLoading(false);
  }, []);

  const setSceneById = (id: number) => {
    const fndScene = scenes.find((scene: any) => {
      return scene.id === id;
    });
    if (fndScene) {
      setScene(fndScene);
    }
  };

  if (showPreview) {
    return (
      <button
        onClick={() => setShowPreview(false)}
        onTouchStart={() => setShowPreview(false)}
        onKeyPress={() => setShowPreview(false)}>
        go quests
      </button>
    );
  }
  if (!loading) {
    return (
      <>
        <Header />
        <Scene scene={scene} setSceneById={setSceneById} />
        {/* <Music music={scene.Music} /> */}
      </>
    );
  } else {
    return (
      <>
        loading4
        <p>{JSON.stringify(data)}</p>
      </>
    );
  }
};

export default IndexPage;

export const query = graphql`
  query {
    strapiQuest {
      Scenes {
        id
        Buttons {
          id
          Text
          Scene {
            id
          }
        }
        Music {
          id
          url
        }
        Person {
          id
          url
          localFile {
            childImageSharp {
              id
              gatsbyImageData
            }
          }
        }
        PersonName
        Styles {
          id
        }
        Text
        Background {
          id
          url
          localFile {
            childImageSharp {
              id
              gatsbyImageData
            }
          }
        }
      }
    }
  }
`;
